import handleLog from '../helpers/reporter';

export class Task {
  #cid       = null;
  #callback  = () => 0;
  #thread_id = null;

  #delay    = 0;
  #debounce = 0;

  #invocationErrorsCount = 0;
  #invocationCount       = 0;
  #invocationLimit       = 0;

  #resolves = [];

  constructor(callback: Function, { delay = 0, debounce = 0, limit = 0 } = {}) {
    this.#cid      = Symbol.for(callback);
    this.#callback = callback;
    this.#delay    = delay;
    this.#debounce = debounce;
  }

  #releaseResolve = (data = null) => {
    if (this.#resolves) {
      let resolve;
      while ((resolve = this.#resolves.shift())) {
        resolve(data);
      }
    }
  };

  #cleanup = () => {
    if (this.#thread_id) {
      clearTimeout(this.#thread_id);
      this.#thread_id = null;
    }
  };

  #invoke = (...args) => {
    this.#cleanup();

    if (this.#invocationLimit && (this.#invocationCount >= this.#invocationLimit)) {
      this.#releaseResolve();
      return false;
    }

    try {
      if (typeof this.#callback === 'function') {
        (async () => {
          const data = await this.#callback(...args);
          this.#invocationCount++;
          this.#releaseResolve(data);
        })();
        return true;
      }
    } catch (e) {
      handleLog(e, 'exception', handleLog.MEDIUM);
      this.#invocationErrorsCount++;
    }

    this.#releaseResolve();

    return false;
  };

  get cid() {
    return this.#cid;
  }

  async invoke(...args) {
    return new Promise(resolve => {
      this.#cleanup();
      this.#thread_id = setTimeout(() => setTimeout(() => resolve(this.#invoke(...args)), this.#delay), this.#debounce);
    });
  }

  getPromise() {
    this.#releaseResolve();
    return new Promise(resolve => this.#resolves.push(resolve));
  }
}

export default class TaskPool {
  #locked  = false;
  #tasks   = [];
  #shift   = true;
  #timeout = null;
  #timer   = null;

  constructor({ shift = true, timeout = 0, autoStart = false } = {}) {
    this.#shift   = shift;
    this.#timeout = timeout;

    if (autoStart) {
      this.start();
    }
  }

  get stats() {
    return `[ QUEUED: ${this.#tasks.length} ]`;
  }

  start() {
    this.stop();
    let next;
    (next = () => this.#timer = setTimeout(() => this.flush(this.#shift) && next(), this.#timeout))();
    return this;
  }

  stop() {
    if (this.#timer) {
      clearTimeout(this.#timer);
      this.#timer = null;
    }
    return this;
  }

  lock() {
    this.#locked = true;
    return this;
  }

  unlock() {
    this.#locked = false;
    return this;
  }

  queue(callback: Function, unique = false) {
    return this.push(callback, unique).getPromise();
  }

  push(callback: Function, unique = false): Task {
    let task = new Task(callback), _task;
    if (unique && (_task = this.#tasks.find((taskB: Task) => task.cid === taskB.cid))) {
      return _task;
    }
    this.#tasks.push(task);
    return task;
  }

  shift(): Task {
    if (this.#tasks.length > 0) {
      return this.#tasks.shift();
    }

    return null;
  }

  pop(): Task {
    if (this.#tasks.length > 0) {
      return this.#tasks.pop();
    }

    return null;
  }

  get(shift = this.#shift): Task {
    return shift ? this.shift() : this.pop();
  }

  flush(...args) {
    if (this.#locked) {
      return;
    }

    this.forceFlush(...args);
    return true;
  }

  forceFlush(...args) {
    const n = (T: Task) => T && setImmediate(async () => await T.invoke(...args) && n(this.get()));
    n(this.get());
    return true;
  }
}