/* @flow */

import fs from '@skpm/fs';
import { decode } from 'sourcemap-codec';
import handleLog from '../helpers/reporter';

type LineMapping = {|
  line: number,
  column: number,
  source: number,
  symbol?: ?number | null,
|};

type SourceLine = LineMapping;

type SketchError = {
  name: string,
  message: string,
  nativeException: { name: () => NSString, reason: () => NSString, userInfo: () => any, callStackSymbols: () => NSString },
  line: number,
  column: number,
  sourceURL: string,
};

type SourceLines = {
  [string]: { [string]: SourceLine }
};

type SourceSegment = { [string]: number };
type SourceSegments = Array<SourceSegment> | any;

type ParsedSourceMap = {
  file: string,
  names: Array<string>,
  sources: Array<string>,
  sourcesContent: Array<string>,
};

export default class SourceMap {
  static cache: ({ [string]: any }) = {};
  map: SourceLines = {};
  #src_filename: string;
  #map_filename: string;

  constructor(filename: string): void {
    this.#src_filename = filename;
    this.#map_filename = filename + '.map';

    if (fs.existsSync(this.#map_filename)) {
      const sourcemap: ParsedSourceMap = SourceMap.cache[this.#map_filename] = SourceMap.cache[this.#map_filename] ||
          (() => {
            const sm = JSON.parse(String(fs.readFileSync(this.#map_filename)));
            sm.sourcesContent = Object.entries(sm.sourcesContent).reduce((a, c) => {
              a[c[0]] = String(c[1]).split('\n');
              return a;
            }, {});
            return sm;
          })();

      this.map = SourceMap.cache[this.#src_filename] = SourceMap.cache[this.#src_filename] ||
          Object.entries(decode(sourcemap.mappings)).reduce(
              (map: SourceLines, [l, segments]) => {
                l = String(parseInt(l) + 1);
                map[l] = map[l] ?? {};

                segments.forEach(segment => {
                  map[l][parseInt(segment[0]) + 1] = {
                    line: parseInt(segment[2]) + 1,
                    column: parseInt(segment[3]) + 1,
                    source: parseInt(segment[1]),
                    symbol: segment[4] ?? null,
                  };
                });

                return map;
              }, {});
    }
  }

  static from(e: SketchError): this {
    return new this(e.sourceURL);
  }

  static print(e: SketchError, highlight = true) {
    console.error('print(e)', e, e.sourceURL, e.line, e.column);
    const m = this.from(e);
    const assoc: SourceLine = m.getAssoc(e.line, e.column);
    if (!assoc) {
      // console.error('BOOTLOADER ERROR');
      // console.error('bad lines', e.line, e.column);
      return;
    }
    // console.error('ASSOC', m.map[e.line]);
    // console.error('ASSOC', m.map[e.line][e.column]);
    const linesOffset = 2;
    const lineLength = (assoc.line + linesOffset).toString().length + 1;
    let errline = m.getSourceContentLines(assoc.source, assoc.line).join('');
    const errlineIndent = errline.length - errline.trimLeft().length;
    errline = errline.trim();
    const pre = m.getSourceContentLines(assoc.source, assoc.line - linesOffset, linesOffset).map(
        (c, l) => (vprintf('% ' + lineLength + 's | %s', assoc.line - linesOffset + l, c))).join('\n');
    const post = m.getSourceContentLines(assoc.source, assoc.line + 1, linesOffset).map(
        (c, l) => (vprintf('% ' + lineLength + 's | %s', assoc.line + linesOffset + l, c))).join('\n');
    const content = vprintf('% ' + lineLength + 's | %s\n% ' + lineLength + 's |% ' + (assoc.column - 1) + 's%s', assoc.line,
        ' '.repeat(errlineIndent) +
        chalk.red(errline),
        '',
        '',
        /*chalk.bgRgb(179, 31, 15).rgb(255,255,255)*/chalk.italic.gray(' ^ ' + e.message + ' '),
    );
    // console.log(, m.getSourceFilename(assoc.source), assoc);
    if (e.nativeException) {
      e.message += '\n' + e.nativeException.name() + ' ' + e.nativeException.reason() + '\n' + e.nativeException.callStackSymbols();
    }
    const tpl = `
${chalk.red(e.name + ' ' + e.message)}: 
${m.getSourceFilename(assoc.source)}:${assoc.line}:${assoc.column}

${[
      pre,
      content,
      post,
    ].join('\n')}
`;
    handleLog(tpl, 'exception', handleLog.CRITICAL);
  }

  static debug(e: SketchError): void {
    console.error('SketchError: ', e);
  }

  getFilename(): string {
    return SourceMap.cache[this.#map_filename]?.file ?? '';
  }

  getSourceSymbol(symbol: number): string {
    return SourceMap.cache[this.#map_filename]?.names?.[symbol] ?? '[global]';
  }

  getSourceContent(source: number): string[] {
    return SourceMap.cache[this.#map_filename]?.sourcesContent?.[source] ?? [''];
  }

  getSourceContentLines(source: number, line: number, count: number = 1): string[] {
    const contents = this.getSourceContent(source);
    return contents.slice(Math.max(line - 1, 0), Math.min(line - 1 + count, contents.length));
  }

  getSourceFilename(source: number): string {
    return SourceMap.cache[this.#map_filename]?.sources?.[source] || '<unknown>';
  }

  getAssoc(line: number, column: number) {
    return this.map?.[line]?.[column] || null;
  }

  getSnippet(): void {

  }

}

