import runFramework from '../framework';

import SpellyChecker from '../modules/SpellyChecker';
import SketchAPI from '../api/SketchAPI';
import Settings from 'sketch/settings';
import Updater from '../modules/Updater';
import Memory from '../modules/Memory';
import Bootscreen from './Bootscreen';
import SpellyAPI from '../modules/SpellyAPI';

/**
 * Plugin framework class
 * @class
 */
export default class Framework {
  constructor() {
    this.spellyAPI = new SpellyAPI();
    this.checkers  = new Map();

    this.debounce = 0;
  }

  initialize(context) {
    let document = context.document;// || SketchAPI.document;

    if (!document) {
      document = void (0);
      // TODO: check is needed returns
      return;
    }

    if (!runFramework()) {
      throw new Error('spellytools Objective C framework was not loaded. Check that file is available and path is correct');
    }

    let spelly = new SpellyChecker(document, this.spellyAPI);

    this.checkers.set(document, spelly);

    if (document) {
      spelly.getWidget(document);
    }

    if (!Memory.token) {
      this.bootscreen = this.bootscreen || new Bootscreen(document, spelly, this.spellyAPI);
      if (document) {
        this.bootscreen.setDocument(document);
      }
    }
  }

  checkAuthForCurrentSession() {
    if (!Memory.token) {
      throw new Error('User not authorized');
    }
  }

  async onSignOut() {
    console.log(' ✅️ [Framework] sign Out');
    Memory.token = '';
  }

  /**
   * Method which triggers when application is initializing
   * Each time plugin starts or initialize
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onInitialize(context) {
    console.log(' ✅️ [Framework] Initialize');
    this.initialize(context);
  }

  async onHandleURL(context) {
    console.log(' ✅️ [Framework] onHandleURL', context);
  }

  /**
   * Method which triggers when sketch is startup
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onStartup(context) {
    console.log(' ✅️ [Framework] onStartup');

    await Updater.checkNewPluginVersion();
  }

  /**
   * Checking is debounce currently busy (in cooldown state   )
   *
   * @return {number}
   */
  isDebounce() {
    if (!this.debounce) {
      this.debounce = (+new Date() / 1000);
      return this.debounce;
    }

    this.debounce = Math.max(((+new Date() / 1000) - this.debounce), 0);

    return this.debounce;
  }

  /**
   * Method which triggers when sketch is startup
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onUndo(context) {
    const { document, action } = context.actionContext;

    let spelly = this.checkers.get(document);

    if (spelly === undefined) {
      return;
    }

    spelly.globalCheck(document.currentPage(), document);

    console.log(' ✅️ [Framework] onUndo', document, action);
  }

  /**
   * Method which triggers when sketch is startup
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onRedo(context) {
    const { document, action } = context.actionContext;

    let spelly = this.checkers.get(document);

    if (spelly === undefined) {
      return;
    }

    spelly.globalCheck(document.currentPage(), document);

    console.log(' ✅️ [Framework] onRedo', document, action);
  }

  /**
   * Method which triggers when plugin is trying to startup
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onRun(context) {
    console.log(' ✅️ [Framework] onRun');
    log('hello onrun');
    // this.initialize(context);

  }

  /**
   * Method which triggers when sketch opening new document
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onOpenDocument(context) {
    console.log(' ✅️ [Framework] onOpenDocument', context.document);

    this.initialize(context);
    let spelly = this.checkers.get(context.document);

    if (spelly === undefined) {
      return;
    }
    spelly.globalCheck(context.document, context.document);
  }

  /**
   * Method which triggers when sketch is closing document
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onCloseDocument(context) {
    console.log(' ✅️ [Framework] onCloseDocument', context.document);
    // this.initialize(context);
    let spelly = this.checkers.get(context.document);

    if (spelly === undefined) {
      return;
    }
    await spelly.dealloc(context.document);
    this.checkers.delete(context.document);

    // @refactor
    Settings.setSessionVariable(String(context.document.documentData().objectID()) + '.currentPageID', '');
  }

  // @refactor
  async onDocumentChanged(context) {
    console.log(' ✅️ [Framework] onDocumentChanged'); //, context, context.document);

    // const inserted = [];
    // const removed  = [];
    const affectedLayers = [];

    const changed = context.actionContext;
    /* global BCComparisonPropertyChange, BCComparisonArrayChange */
    Array.from(changed).forEach(change => {
      const refObject = change.object();
      const object    = SketchAPI.findObject(String(refObject.objectID())) || refObject;

      if (change instanceof BCComparisonArrayChange) {
        if (change.isInsertion()) {
          affectedLayers.push(object);
        }
        if (change.isRemoval()) {
          affectedLayers.push(object);
        }
      }
      if (change instanceof BCComparisonPropertyChange) {
      }
    });

    if (affectedLayers.length) {
      const document = SketchAPI.document;
      let spelly     = this.checkers.get(document);

      if (spelly === undefined) {
        return;
      }
      await spelly.removedLayers(affectedLayers, document);
      setTimeout(() => spelly.globalCheck(document, document), 500);
    }
  }

  async onTextChanged(event) {
    console.log(' ✅️ [Framework] onTextChanged', event);
    // if (event.old !== event.new) {
    //     await this.spelly.checkSentence(event.layer.objectID(), event.layer, event.new);
    // }
  }

  async onCheck(context) {
    console.log(' ✅️ [Framework] onCheck');
    // setImmediate(() => {
    //     // this.spelly.globalCheck(SketchAPI.document);
    //     this.spelly.globalCheck(SketchAPI.document.currentPage());
    // });
    // this.initialize(context);
  }

  getPos(layer, ctx) {
    let x      = layer.frame().x(), y = layer.frame().y();
    let parent = layer;
    while ((parent = parent.parentGroup())) {
      x += parent.frame().x();
      y += parent.frame().y();
    }

    const point = CGPointMake(x, y);

    const abs = ctx.document.contentDrawView().convertPointFromPage(point);
  }

  /**
   * Method which triggers when canvas selection changing
   * @method
   * @param context
   * @returns {Promise<void>}
   */
  async onSelectionChanged(context) {
    console.log(' ✅️ [Framework] onSelectionChanged', context.document);

    // this.initialize(context);

    // @refactor to external
    const document: MSDocument = context.document;
    const oldPageID            = Settings.sessionVariable(String(document.documentData().objectID()) + '.currentPageID') || null;
    const newPageID            = String(context.document.currentPage().objectID());
    Settings.setSessionVariable(String(document.documentData().objectID()) + '.currentPageID', newPageID);

    if (oldPageID !== newPageID) {
      this.pageChanged(SketchAPI.findObject(oldPageID), document.currentPage(), context);
    }

    if (context.oldSelection.length > 1) {
      console.warn('❓ onSelectionChanged to much selection', context.oldSelection);
      return;
    }

    // todo: check only text layers or symbol instances
    context.oldSelection.forEach(masterLayer => {
      if (!SketchAPI.findObject(masterLayer.objectID())) {
        // this.spelly.removedLayers(masterLayer, context.document);
        return;
      }

      let spelly = this.checkers.get(context.document);

      if (spelly === undefined) {
        return;
      }

      if (!spelly.validType(masterLayer)) {
        return;
      }

      spelly.globalCheck(masterLayer, context.document);
    });
  }

  async pageChanged(from: MSPage, to: MSPage, context: SketchContext) {
    let spelly = this.checkers.get(context.document);
    if (spelly === undefined) {
      return;
    }
    await spelly.pageChanged(from, to, context.document);
  }

  onRestart(context) {
    console.log(' ✅️ [Framework] onRestart', { context });
  }
};
