import {areaToRange} from "../../helpers/helpers";
import SketchAPI from "../SketchAPI";
import handleLog from "../../helpers/reporter";

/**
 * Wrapper for layer highlighting
 *
 * @class
 */
export default class HighlightedLayer {
  /**
   * layerWithPath:
   *
   * @constructor
   * @param {MSTextLayer} layer
   * @param target
   * @param {boolean} apply
   * @param {MSLayer} masterLayer
   */
  constructor(layer, target, apply, masterLayer) {
    const area = target.data;
    this.layer = layer;
    this.masterLayer = masterLayer;
    this.range = areaToRange(area);

    this.apply = this.m_apply.bind(this);
    this.restore = this.m_restore.bind(this);
    this.blink = this.m_blink.bind(this);

    if (apply) {
      this.m_apply();
    }
  }

  /**
   * Start blink effect
   *
   * @method
   * @param times how many times it will blink
   * @param interval interval between blinks
   * @returns {Promise<HighlightedLayer>}
   */
  async m_blink(times = 20, interval = 120) {
    if (this.layer.select_byExpandingSelection) {
      this.layer.select_byExpandingSelection(true, false);
    } else if (this.masterLayer.select_byExpandingSelection) {
      this.masterLayer.select_byExpandingSelection(true, false);
    }

    let util = STUtil.alloc().init()

    try {
      util.focusTo_withDocument_withOffset_withMaster(this.layer, SketchAPI.document, NSMakeRange(this.range.offset, this.range.length), this.masterLayer);
    } catch (e) {
      handleLog(e, 'exception', handleLog.MEDIUM);
    }

    try {
      util.highlightBlink_withDocument_withOffset_withMaster(this.layer, SketchAPI.document, NSMakeRange(this.range.offset, this.range.length), this.masterLayer);
    } catch (e) {
      handleLog(e, 'exception', handleLog.MEDIUM);
    }

    return this;
  }

  /**
   * Apply highlight styles
   * @method
   */
  async m_apply() {
    let util = STUtil.alloc().init()
    util.highlight_withDocument(this.layer, SketchAPI.document);
  }

  /**
   * Restore default styles
   * @method
   */
  m_restore() {
    return this;
  }

  destroy() {
    return this;
  }
}
