Array.prototype.chunk = function (size) {
    return [].concat.apply([],
        this.map((elem, i) => {
            return i % size ? [] : [this.slice(i, i + size)];
        }),
    );
};

Array.prototype.toAssoc = function (key) {
    return this.reduce((acc, cur) => {
        if (typeof key == 'string') {
            acc[cur[key]] = cur;
        } else if (typeof key == 'function') {
            const _key = key(cur);
            if (typeof _key != 'undefined' && !!key) {
                acc[_key] = cur;
            }
        }
        return acc;
    }, {});
};