/**
 * This code runs objective-C spellytools framework from `spellytools.framework/spellytools` file
 * @return {boolean}
 */
export default function () {
  const frameworkName = 'spellytools';
  const scriptPath = COScript.currentCOScript().env().scriptURL.path();
  const directory = scriptPath.replace(/Sketch\/.*\.js/, 'Sketch');

  const mocha = Mocha.sharedRuntime();

  if (mocha.valueForKey_(frameworkName)) {
    return true;
  } else if (mocha.loadFrameworkWithName_inDirectory_(frameworkName, directory)) {
    mocha.setValue_forKey_(true, frameworkName);
    return true;
  } else {
    return false;
  }

}
