import sketch, {UI} from 'sketch';

import Data from '../helpers/data';
import SketchAction from "./wrappers/SketchAction";
import HighlightedLayer from "./wrappers/HighlightedLayer";
import sleep from "../helpers/sleep";
import { areaToRange, CGRectToRectangle, getDocumentFrom } from '../helpers/helpers';
import {Rectangle} from "sketch/dom";

/**
 * Sketch API helpers wrapper
 * @class
 */
export default class SketchAPI {

    /**
     * @constructor
     * @param context
     */
    constructor(context = null) {
        this._context = context;
        this._document = null;
    }

    /**
     * Get Sketch selected layers
     * @property
     * @static
     * @returns {MSLayer[]}
     */
    static get selected() {
        return SketchAPI.document.selectedLayers().layers();
    }

    /**
     * Set zoom value of currently active document
     *
     * @param value
     */
    static set zoom(value) {
        this.document.zoomValue = value;
    }

    /**
     * Get zoom value of currently active document
     *
     * @return {number | any}
     */
    static get zoom() {
        return this.document.zoomValue();
    }

    /**
     * Set Sketch selected layers
     * @method
     * @param {MSLayer[]} value
     */
    static set selected(value) {
        return SketchAPI.document.selectedLayers().setLayers(value);
    }

    /**
     * Get list of Sketch tool actions
     * @property
     * @static
     * @returns {SketchAction[]}
     */
    static get m_actions() {
        if (typeof SketchAPI._actions_map !== 'undefined') {
            return SketchAPI._actions_map;
        }

        SketchAPI._actions_map = {};

        const SketchActions = SketchAPI.document.actionsController().allActions();
        for (let i = 0; i < SketchActions.length; i++) {
            const sketchAction = SketchActions[i];
            const actionClass = sketchAction.toString().substr(1).split(':')[0].trim();
            SketchAPI._actions_map[actionClass] = new SketchAction(sketchAction);
        }

        return SketchAPI._actions_map;
    }

    /**
     * Get native Sketch selected document
     * @property
     * @static
     * @returns {MSDocument}
     */
    static get document() {
        return this.m_document ? (this.m_document.sketchObject || null) : null;
    }

    /**
     * Get wrapper Sketch selected document
     * @property
     * @static
     * @returns {Document}
     */
    static get m_document() {
        return sketch.getSelectedDocument() || null;
    }

    /**
     * Get current Sketch context or use last one if available
     * @property
     * @returns {*}
     */
    get context() {
        return this._context || Data.get('context');
    }

    /**
     * Find Sketch action in tool menu
     * @method
     * @static
     * @returns {SketchAction|null}
     */
    static getAction(action) {
        return SketchAPI.m_actions[action.toString()] || null;
    }

    /**
     * Get wrapper Sketch selected document
     * @method
     * @static
     * @param document
     * @returns {*|MSDocument}
     */
    static getDocument(document) {
        return document || this.document;
    }

    /**
     * Find symbol instance by overrideID
     * @method
     * @static
     * @param symbol
     * @param overrideID
     * @returns MSImmutableLayer|_MSImmutableLayer|MSImmutableTextLayer|_MSImmutableTextLayer|MSImmutableStyledLayer|_MSImmutableStyledLayer||null|*
     */
    static findSymbolInstance(symbol, overrideID) {
        const children = symbol.modifiedMaster().layers();

        for (let i = 0; i < children.length; i++) {
            const layer = children[i];
            if (String(layer.objectID()) === String(overrideID)) {
                return layer;
            }
        }

        return null;
    }

    /**
     * Find object by given objectID
     * Returns null if not found
     * @method
     * @param {string} objectID
     * @returns {*|MSLayer|MSTextLayer|null}
     */
    static findObject(objectID, document = null) {
        const mDocument = document || SketchAPI.m_document;

        if (!mDocument || !mDocument.getLayerWithID) {
            return null;
        }

        const layer = mDocument.getLayerWithID(objectID);

        if (!layer) {
            return null;
        }

        return layer.sketchObject || null;
    }

    /**
     * Showing message tooltip
     * @method
     * @param {string} message
     */
    static message(message) {
        UI.message(message, SketchAPI.m_document);
    }

    /**
     * Focus and zoom canvas to given layer
     * @method
     * @param {*|MSLayer} layer
     * @param {CGRect} rect
     * @returns {Promise<void>}
     */
    static async focusTo(layer, rect = null) {

        SketchAPI.document.setCurrentPage(SketchAPI.findLayerPage(layer));

        // await sleep(200);
        // SketchAPI.selected = [layer];
        // layer.select_byExpandingSelection(true, false);
        // await sleep(200);

        const layerRect = rect || layer.absoluteRect().rect();
        const contentDrawView = SketchAPI.document.contentDrawView();

        contentDrawView.centerRect_(layerRect);
        contentDrawView.zoomToFitRect_(layerRect);
    }

    static findLayerPage(layer) {
        let _layer = layer;
        while (!(_layer instanceof MSPage)) {
            _layer = _layer.parentGroup();
        }
        return _layer;
    }

    /**
     * Highlights layer
     * @method
     * @param {MSTextLayer} layer
     * @param {object} target
     * @param {boolean} apply
     * @param {null|MSLayer} masterLayer
     * @returns {HighlightedLayer} Highlight wrapper
     */
    static highlight(layer, target, apply = false, masterLayer = null) {
        return new HighlightedLayer(layer, target, apply, masterLayer);
    }

}
