const EventLoop       = require('./api/EventLoop').default;
const Data            = require('./helpers/data').default;
const { api_version } = require('./helpers/consts');
const setUpLogs       = require('./setUpLogs').default;

global['NSLog']('⚡️ [' + __threadID + '] RUNTIME START');

(() => {
  /** Runtime initialization */
  Object.assign(global, require('./globals.h.js'));
  require('./extensions');
  if (typeof regeneratorRuntime === 'undefined') {
    require('babel-polyfill');
  }
  if (__pluginThread.runtime) {
    // We should not execute the rest of code if in runtime
    return;
  }
  __pluginThread.runtime = true;
  global['NSLog']('🧠️ [' + __threadID + '] RUNTIME INIT');
  /** end runtime initialization */

  setUpLogs();

  const Framework = new (require('./components/Framework').default)();

  EventLoop.on('onUndo', context => Framework.onUndo(context));
  EventLoop.on('onRedo', context => Framework.onRedo(context));
  EventLoop.on('onCheck', context => Framework.onCheck(context));
  EventLoop.on('onHandleURL', context => Framework.onHandleURL(context));
  EventLoop.on('onSelectionChanged', context => Framework.onSelectionChanged(context));
  EventLoop.on('onDocumentChanged', context => Framework.onDocumentChanged(context));
  EventLoop.on('onTextChanged', context => Framework.onTextChanged(context));
  EventLoop.on('onOpenDocument', context => Framework.onOpenDocument(context));
  EventLoop.on('onCloseDocument', context => Framework.onCloseDocument(context));
  EventLoop.on('onStartup', context => Framework.onStartup(context));
  EventLoop.on('onRestart', context => Framework.onRestart(context));
  EventLoop.on('onRun', context => Framework.onRun(context));
  EventLoop.on('onInitialize', context => Framework.onInitialize(context));
  EventLoop.on('onSignOut', context => Framework.onSignOut(context));

  EventLoop.start();
})();

export default async context => EventLoop.emit('onInitialize', context);
export var onHandleURL        = async context => EventLoop.emit('onHandleURL', context);
export var onCheck            = async context => EventLoop.emit('onCheck', context);
export var onUndo             = async (document, action) => EventLoop.emit('onUndo', document, action);
export var onRedo             = async (document, action) => EventLoop.emit('onRedo', document, action);
export var onSelectionChanged = async context => EventLoop.emit('onSelectionChanged', context.actionContext);
export var onDocumentChanged  = async context => EventLoop.emit('onDocumentChanged', context);
export var onTextChanged      = async context => EventLoop.emit('onTextChanged', context.actionContext);
export var onOpenDocument     = async context => EventLoop.emit('onOpenDocument', context.actionContext);
export var onCloseDocument    = async context => EventLoop.emit('onCloseDocument', context.actionContext);
export var onRestart          = async context => EventLoop.emit('onRestart', context.actionContext);
export var onRun              = async context => EventLoop.emit('onRun', context.actionContext);
export var run                = async context => EventLoop.emit('onRun', context.actionContext);
export var onSignOut          = async context => EventLoop.emit('onSignOut', context.actionContext);
export var onStartup          = async context => {
  const _v           = require('sketch/async').version;
  api_version.sketch = String(_v.sketch);
  api_version.api    = String(_v.api);
  api_version.spelly = String(context.plugin.version());

  // todo: refactor
  Data.set('widget.view', context.plugin.urlForResourceNamed('build/widget.html'));

  Data.set('bootscreen.view', context.plugin.urlForResourceNamed('build/bootscreen.html'));
  Data.set('settings.view', context.plugin.urlForResourceNamed('build/settings.html'));
  Data.set('login.view', context.plugin.urlForResourceNamed('build/login.html'));
  Data.set('register.view', context.plugin.urlForResourceNamed('build/register.html'));
  Data.set('settings.view', context.plugin.urlForResourceNamed('build/settings.html'));

  Data.set('feedback.view', context.plugin.urlForResourceNamed('legacy/feedback.html'));
  Data.set('changelog.view', context.plugin.urlForResourceNamed('legacy/changelog.html'));

  EventLoop.emit('onStartup', context);
};

global['NSLog']('⛔️️ [' + __threadID + '] RUNTIME EXIT');

