const { DEBUG, ENNSLOG, LOGFILTER } = require('../helpers/consts');

module.exports = {
  _NSLog(input, ...args) {
    if (LOGFILTER) {
      if (
          (typeof LOGFILTER === 'string' && !String(input).includes(LOGFILTER)) ||
          !input.match(LOGFILTER)
      ) {
        return 0;
      }
    }

    eval('NSLog(input,' + args.map((a, i) => 'args[' + i + ']').join(',') + ');');
  },
};