import Settings from 'sketch/settings';

/**
 * Sketch settings to data storage bridge
 *
 * @class
 */
export default class DataStorage {
    constructor(namespace) {
        this.namespace = namespace;
    }

    /**
     * Get storage proxy
     *
     * @method
     * @static
     * @returns {DataStorage|Proxy}
     */
    static get() {
        return new Proxy({}, {
            set: (target, property, value) => {
                Settings.setSettingForKey(target.namespace + '.' + property, JSON.stringify(value));

                if(typeof Reflect !== 'undefined') {
                    return Reflect.set(target, property, value);
                } else {
                    return target = value;
                }
            },

            get: (target, property) => {
                target = target || {};

                if (typeof property !== 'string') {
                    return target[property];
                }

                const value = JSON.parse(
                    (Settings.settingForKey(target.namespace + '.' + property) || 'null'),
                ) || void (0);

                if (value === undefined) {
                    const defaultKey = property + 'Default';
                    if (typeof target[defaultKey] === 'function') {
                        return target[defaultKey]();
                    } else if (target[defaultKey] !== undefined) {
                        return target[defaultKey];
                    }
                }

                if (target[property] !== undefined) {
                    return target[property];
                }

                return value;
            },
        });
    }
}