import {Rectangle as TRectangle} from "sketch/dom";

/**
 * @property x
 * @property y
 * @property width
 * @property height
 */
export class Rectangle extends TRectangle {
    /**
     * Return a new Rectangle object for a given x,y, width and height.
     *
     * @param {number | Rectangle} x The x coordinate of the top-left corner of the rectangle. Or a Rectangle.
     * @param {number} y The y coordinate of the top-left corner of the rectangle.
     * @param {number} width The width of the rectangle.
     * @param {number} height The height of the rectangle.
     * @return The new Rectangle object.
     */
    constructor(x, y, width, height) {
        super(x, y, width, height);
    }

    /**
     * Check is rectangle contains {Rectangle}
     *
     * @param {Rectangle} rectangle
     */
    containsRectangle(rectangle) {
        return (
            this._x <= rectangle.x && (rectangle.x + rectangle.width) <= (this._x + this._width) &&
            this._y <= rectangle.y && (rectangle.y + rectangle.height) <= (this._y + this._height)
        );
    }

    /**
     * Check is rectangle contains Point by x, y
     *
     * @param {number} x
     * @param {number} y
     * @return {boolean|boolean}
     */
    containsPoint(x, y) {
        return (
            this._x <= x && x <= (this._x + this._width) &&
            this._y <= y && y <= (this._y + this._height)
        );
    }

    /**
     * Adds rectangle vector
     *
     * @param {Rectangle[]} rectangles
     */
    add(...rectangles) {
        for (let rectangle of rectangles) {
            this._x += rectangle.x;
            this._y += rectangle.y;
            this._width += rectangle.width;
            this._height += rectangle.height;
        }
    }

    /**
     * Subtract rectangle vector
     *
     * @param {Rectangle[]} rectangles
     */
    subtract(...rectangles) {
        for (let rectangle of rectangles) {
            this._x -= rectangle.x;
            this._y -= rectangle.y;
            this._width -= rectangle.width;
            this._height -= rectangle.height;
        }
    }

    /**
     * Merging rectangles
     *
     * @param {Rectangle} rectangle
     */
    merge(rectangle) {
        throw new Error('Not implemented');
    }

    /**
     * Merge {MSRect} from array of {MSLayer} to {Rectangle}
     *
     * @param {MSLayer[]|*} nativeObjects
     *
     * @return {Rectangle}
     */
    static fromNativeArray(...nativeObjects) {
        throw new Error('Not implemented');

        const rectangle = new this();

        for (let nativeObject of nativeObjects) {
            rectangle.merge(this.fromNative(nativeObject));
        }

        return rectangle;
    }

    /**
     * Converts {MSLayer} to {Rectangle}
     *
     * @param {MSLayer|*} nativeObject
     *
     * @return {Rectangle}
     */
    static fromNative(nativeObject) {
        if (!nativeObject.frame) {
            return new this(0, 0, 0, 0);
        }
        return this.fromMSRect(nativeObject.frame());
    }

    /**
     * Converts {CGRect} to {Rectangle}
     *
     * @param {CGRect} rect
     *
     * @return {Rectangle}
     */
    static fromCGRect(rect) {
        return new this(
            rect.origin.x,
            rect.origin.y,
            rect.size.width,
            rect.size.height,
        );
    }

    /**
     * Converts {MSRect} to {Rectangle}
     *
     * @param {MSRect} rect
     *
     * @return {Rectangle}
     */
    static fromMSRect(rect) {
        return new this(
            rect.x(),
            rect.y(),
            rect.width(),
            rect.height(),
        );
    }

    toString() {
        return '[ Rectangle ' + JSON.stringify({
            x: this.x,
            y: this.y,
            width: this.width,
            height: this.height,
        }, null, 2) + ' ] ';
    }
}