import { API_HOST, api_version } from '../helpers/consts';
import Memory from './Memory';
// import fetchTimeout from 'fetch-timeout';

export default class SpellyAPI {
  constructor() {
    this._server_error  = 'Server error, please contact support';
    this._timeout_error = 'Timeout try again';
    this.api_host       = API_HOST;
    this.token          = '';
    this.userInfo       = { email: '', plan: '', status: '', type: '', team: '', lang: '', daysLeft: '', role: '' };
    if (Memory.token) {
      this.token = Memory.token;
      this.setInfo().then(r => console.log(r));
    }
  }

  logOut() {
    this.setToken('');
  }

  async getUserInfo() {
    if (!Memory.token) {
      await this.setInfo();
    }
    return this.userInfo;
  }

  async setInfo() {
    let response = await this.getInfo();
    if (response.result === true) {
      this.userInfo = response.userInfo;
      // console.log("=S= Response token",  response.token)
      return this.setToken(response.token);
    }
    // console.log("=S= No token")
    return this.setToken('');
  }

  setToken(token) {
    if (typeof token === 'string') {
      // console.log("=S= Set token: ", token)
      Memory.token = token;
      this.token   = token;
    } else {
      console.log('=S= So wrong', token);
      Memory.token = '';
      this.token   = '';
    }
  }

  openURL(path) {
    NSWorkspace.sharedWorkspace().openURL(NSURL.URLWithString(this.api_host + path));
  }

  isAuth() {
    return this.token !== '';
  }

  getInfo() {
    return this.postRequest('/api/info');
  }

  async validatePin(data) {
    let response = await this.postRequest('/validate_pin', data);

    if (response.hasOwnProperty('token')) {
      this.setToken(response.token);
      this.userInfo = response.userInfo;
      console.log('=S= Set UserInfo', this.userInfo);
    }
    return response;
  }

  async validatePassword(data) {
    let response = await this.postRequest('/validate_password', data);

    if (response.hasOwnProperty('token')) {
      this.setToken(response.token);
      this.userInfo = response.userInfo;
    }
    return response;
  }

  resendPin(data) {
    data['resend'] = true;
    return this.postRequest('/send_pin', data);
  }

  getStarted(data) {
    return this.postRequest('/send_pin', data);
  }

  check(data) {
    return this.postRequest('/api/check', data, 60000);
  }

  postRequest(url, data, timeout = 2000) {
    console.log('postRequest', {data, url});
    let headers = {
      'Content-Type': 'application/json;charset=utf-8',
      'PluginVersion': api_version.spelly,
      'SketchVersion': api_version.sketch,
    };

    if (this.token !== '') {
      headers['Authorization'] = 'Bearer ' + this.token;
    }

    data = {
      method: 'POST',
      headers: headers,
      body: JSON.stringify(data),
    };

    return this.fetchRequestWithTimeout(this.api_host + url, data, timeout);
  }

  async fetchRequestWithTimeout(url, data, timeout) {
    try {
      return await this.fetchTimeout(url, data, timeout, this._timeout_error)
                       .then(res => {
                         if (res.status !== 200) {
                           return { result: false, message: this._server_error };
                         } else {
                           return res;
                         }
                       })
                       .then(json => {
                         try {
                           return json.json();
                         } catch (e) {
                           return this.responsePromise(this._server_error);
                         }
                       });
    } catch (e) {
      return this.responsePromise(e);
    }
  }

  fetchTimeout(url, options, timeout, error) {
    error   = error || 'Timeout error';
    options = options || {};
    timeout = timeout || 10000;
    return this.timeoutPromise(fetch(url, options), timeout, error);
  };

  timeoutPromise(promise, timeout, error) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        reject(error);
      }, timeout);
      promise.then(resolve, reject);
    });
  }

  responsePromise(message) {
    return new Promise((resolve, _) => {
      resolve({
        result: false,
        message: message === this._timeout_error ? message : this._server_error,
      });
    });
  }

}

