import Settings from 'sketch/settings';

/**
 * @class WordDictionary
 */
export default class WordDictionary {
    constructor() {
        this.learned = {};
        this.ignored = {};

        this._cache_key = '@spelly.dictionary';

        this.load();
    }

    /**
     * Get dictionary object
     *
     * @return {{ignored: {}, learned: {}}}
     */
    get dict() {
        return {
            learned: this.learned,
            ignored: this.ignored,
        };
    }

    /**
     * @return {WordDictionary}
     */
    static get() {
        return WordDictionary._instance || (WordDictionary._instance = new WordDictionary());
    }

    /**
     * Check is word in learned dictionary
     *
     * @param {string} word
     * @return {boolean}
     */
    isLearned(word) {
        word = word.trim().toLowerCase();
        return !!this.learned[word];
    }

    /**
     * Check is word in ignored dictionary
     *
     * @param {string} word
     * @return {boolean}
     */
    isIgnored(word) {
        word = word.trim().toLowerCase();
        return !!this.ignored[word];
    }


    /**
     * Add word in learned dictionary
     *
     * @param {string} word
     * @param {boolean} save
     * @return {WordDictionary}
     */
    learn(word, save = false) {
        word = word.trim().toLowerCase();
        this.learned[word] = 1;

        if (save) {
            this.save();
        }

        return this;
    }

    /**
     * Remove word from learned dictionary
     *
     * @param {string} word
     * @param {boolean} save
     * @return {WordDictionary}
     */
    forget(word, save = false) {
        word = word.trim().toLowerCase();
        delete this.learned[word];

        if (save) {
            this.save();
        }

        return this;
    }

    /**
     * Add word in ignored dictionary
     *
     * @param {string} word
     * @param {boolean} save
     * @return {WordDictionary}
     */
    ignore(word, save = false) {
        word = word.trim().toLowerCase();
        this.ignored[word] = 1;

        if (save) {
            this.save();
        }

        return this;
    }

    /**
     * Remove word from ignored dictionary
     *
     * @param {string} word
     * @param {boolean} save
     * @return {WordDictionary}
     */
    stopIgnore(word, save = false) {
        word = word.trim().toLowerCase();
        delete this.ignored[word];

        if (save) {
            this.save();
        }

        return this;
    }

    /**
     * Save current dictionary state
     *
     * @return {WordDictionary}
     */
    save() {
        Settings.setSettingForKey(this._cache_key, {
            learned: this.learned,
            ignored: this.ignored,
        });

        return this;
    }

    /**
     * Load dictionary state
     *
     * @return {WordDictionary}
     */
    load() {
        const data = Settings.settingForKey(this._cache_key) || {
            learned: {},
            ignored: {},
        };

        this.learned = Object.assign(data.learned, this.learned);
        this.ignored = Object.assign(data.ignored, this.ignored);

        return this;
    }
}
