import {UI} from 'sketch';
import {serialize} from "./core.helpers";

/**
 * Global Sketch toast logger
 *
 * @module
 * @function
 * @param {*} args
 */
export function toast(...args) {
  UI.message(args.map(e => {
    if (typeof e === 'string') {
      return e;
    }

    if (typeof e === 'object') {
      return serialize(e);
    }

    if (typeof e === 'undefined') {
      return 'undefined';
    }

    if (e === null) {
      return 'null';
    }

    return e.toString();
  }).join(' '));
}