import * as util from 'util';

export function serialize(obj, {
                            depth = 0,
                            compact = false,
                            colors = false,
                            showHidden = false,
                            showProxy = false,
                            maxArrayLength = 5000,
                            breakLength = 0,
                            getters = true,
                            min = false,
                          } = {},
) {
  if (min) {
    compact        = true;
    depth          = 6;
    maxArrayLength = 5;
    breakLength    = 120;
  }

  return util.inspect(obj, {
    colors,
    compact,
    depth,
    showHidden,
    showProxy,
    maxArrayLength,
    breakLength,
    getters,
  });
}
