const MainThread = NSThread.mainThread().threadDictionary();

const DICT_KEY = 'Spelly::data::';

/**
 * Serialized global data storage
 *
 * @class
 */
class Data {
    /**
     * Get global data property
     *
     * @method
     * @param {string} key property name
     * @param {string} defaultValue default value if property does't exists
     * @returns {*}
     */
    static get(key, defaultValue = null) {
        return MainThread[DICT_KEY + key] = (
            MainThread[DICT_KEY + key] ||
            (
                typeof defaultValue == 'function' ? defaultValue() : defaultValue
            )
        );
    }

    /**
     * Set global data property
     *
     * @method
     * @param {string} key property name
     * @param {any} value value to be written
     * @returns {*}
     */
    static set(key, value) {
        return MainThread[DICT_KEY + key] = typeof value === 'function' ? value() : value;
    }
}

export const INITIALIZED = 'initialized';

export default Data;