import SourceMap from './packages/SourceMap';

export default function debugExecute(main, defaultValue = {}, ...args) {
  try {
    return main.apply(global, args);
  } catch (e) {

    try {
      console.log(' ⛔️ debugExecute GLOBAL catch');
      SourceMap.debug(e);
    } catch (re) {
      print('[ MAY BE CAUSED BY NEXT ERROR ] ' + re.name + ': ' + re.message + '\n  at ' + re.stack.replace(/\n/g, '\n  at ') + '\n\n');
      if(e.nativeException) {
        print(' - Obj-C Exception:\n' + e.nativeException.name() + ': ' + e.nativeException.reason() + '\n  at ' + e.nativeException.callStackSymbols() + '\n\n - JavaScript Exception:');
      }
      print(e.name + ': ' + e.message + '\n  at ' + e.stack.replace(/\n/g, '\n  at '));
    }
  }

  return defaultValue;
}
