type Listener = (...args: any[]) => void
type Events = { [event: string]: Listener[] };

__pluginThread.eventLoop = __pluginThread.eventLoop || {
  sharedPool: [],
};

export default class EventLoop {
  static _timer: number  = null;
  static _events: Events = {};

  static start(): void {
    this.free();
    this._timer = setInterval(() => {
      const e = __pluginThread.eventLoop.sharedPool.shift();
      if (!e) {
        return;
      }
      this._emit(e.event, ...e.args);
    }, 400);
  }

  static free(): void {
    if (this._timer) {
      clearInterval(this._timer);
    }
  }

  static off(event: string, listener: Listener) {
    let idx;

    if (typeof this._events[event] !== 'undefined') {
      idx = this._events[event].indexOf(listener);

      if (idx > -1) {
        this._events[event].splice(idx, 1);
      }
    }
  }

  static on(event: string, listener: Listener): () => void {
    if (typeof this._events[event] === 'undefined') {
      this._events[event] = [];
    }
    const idx = this._events[event].push(listener) - 1;
    return () => this._events[event].splice(idx, 1);
  }

  static once(event: string, listener: Listener): void {
    const off = this.on(event, () => {
      off();
      listener(...arguments);
    });
  }

  static emit(event, ...args): void {
    __pluginThread.eventLoop.sharedPool = [...Array.from(__pluginThread.eventLoop.sharedPool), { event, args }];
  }

  static _emit(event, ...args): void {
    if (typeof this._events[event] !== 'undefined') {
      this._events[event].forEach(listener => listener(...args));
    }
  }
}
