import {WebViewDelegate, WebViewUIDelegate} from "../helpers/WebViewDelegate";
import {getDocumentFrom, getFrame, getTheme, setFrame} from "../helpers/helpers";
import EventEmitter from "../modules/EventEmitter";
import {toast} from "../helpers/debug";

/**
 * NSWebKit Wrapper
 * @class
 */
export default class WebKitForm extends EventEmitter {
  /**
   * @constructor
   * @param document
   * @param xib
   * @param view
   */
  constructor(document, xib, view) {
    super();

    this.document = getDocumentFrom(document);

    this.xib = xib;
    this.view = this.xib.getRoot();

    this.stack = '';
    this.setState = state => {
      // this.stack += 'setState(' + JSON.stringify(state) + ');\n';
      this.call('setState', state);
    };


    this.view_uri = view;

    this.webView = this.xib.webView;
    this.windowObject = this.webView.windowScriptObject();

    this.webView.setFrameLoadDelegate_(WebViewDelegate(this).getClassInstance());
    this.webView.setUIDelegate_(WebViewUIDelegate(this).getClassInstance());
    this.webView.setDrawsBackground(false);
    this.webView.autoresizingMask = NSViewWidthSizable | NSViewHeightSizable;
    this.reload();

    this._rect = {
      x: 0,
      y: 0,
      width: 0,
      height: 0,
    };

    this.call_queue = [];
    this.boot = false;
    this.on('boot', () => {
      this.boot = true;
      const queue = this.call_queue.slice(0);
      this.call_queue = [];
      for (let deferredCall of queue) {
        deferredCall.resolve(this.call(deferredCall.func, ...deferredCall.params));
      }
    });

    this.on('updateTheme', () => {
      this.call('updateTheme', getTheme());
    });

    this.emit('updateTheme');

    this.show();
  }

  get rect() {
    this.updateFrameRect();
    return this._rect;
  }

  set rect(rect) {
    this.updateFrameRect();

    rect = rect || {};

    rect.x = rect.x || this._rect.x || 0;
    rect.y = rect.y || this._rect.y || 0;
    rect.width = rect.width || this._rect.width || 0;
    rect.height = rect.height || this._rect.height || 0;

    this._rect.x = rect.x;
    this._rect.y = rect.y;
    this._rect.width = rect.width;
    this._rect.height = rect.height;

    this.view.setFrame(NSMakeRect(
        rect.x,
        rect.y,
        rect.width,
        rect.height,
    ));
  }

  /**
   * Replace current frame view to given one
   *
   * @method
   * @param {NSView} view
   */
  set frameView(view) {
    setFrame(this.view, {
      ...getFrame(view),
    });
  }

  /**
   * Change form visibility
   *
   * @method
   * @param {boolean} visible
   * @returns {*}
   */
  set visible(visible) {
    this.view.setHidden(!visible);
    return visible;
  }

  reload() {
    this.webView.mainFrame().loadRequest(
        NSURLRequest.requestWithURL(this.view_uri),
    );
  }

  updateFrameRect() {
    const frame = this.view.frame();
    this._rect.x = frame.origin.x;
    this._rect.y = frame.origin.y;
    this._rect.width = frame.size.width;
    this._rect.height = frame.size.height;
  }

  /**
   * Set onClick action for button on view form, by reference pointer
   *
   * @method
   * @param {string} btn Reference pointer to button on view form
   * @param {function} action Event callback
   */
  addOnClick(btn, action) {
    console.info('Binding ' + btn + ' action ' + action + ' for element on form');

    if (!this.xib[btn]) {
      console.warn('No ' + btn + ' element found on form for action ' + action);
      return;
    }

    this.xib[btn].setCOSJSTargetFunction(sender => action(sender));
  }

  /**
   * Set form visibility to visible
   *
   * @method
   */
  show() {
    this.view.setHidden(false);
  }

  /**
   * Set form visibility to hidden
   *
   * @method
   */
  hide() {
    this.view.setHidden(true);
  }

  /**
   * Show toast message
   *
   * @param {*} args Test message
   */
  toast(...args) {
    toast(...args);
  }

  /**
   * Log message to host application
   *
   * @method
   * @param {*} args Array of arguments passed thru
   */
  log(...args) {
    //console.log.apply(console, [chalk.blueBright('[RPC]'), ...args]);
  }

  /**
   * Call remote webkit function
   *
   * @method
   * @param {string} func String reference to remote function
   * @param {*} params Array of arguments passed thru
   * @returns {any|null}
   */
  async call(func, ...params) {
    if (!this.boot) {
      return new Promise(resolve => {
        this.call_queue.push({func, params, resolve});
      });
    }
    let json = this
        .windowObject
        .evaluateWebScript(
            'JSON.stringify( ' + func + '.apply(' + func + ', ' + JSON.stringify(params) + ') || null )',
        ) || 'null';

    if (json instanceof WebUndefined) {
      json = 'null';
    }

    json = json.urlDecode();

    return JSON.parse(
        json,
    );
  }
}
