import Promise from 'bluebird';
import { chunkAssoc, sentences_compress } from '../helpers/helpers';
import Memory from '../modules/Memory';

export const CACHE_KEY = '@spelly.data';

/**
 * Spell checker module
 * @class
 */
export default class Spellchecker {

  /**
   * @constructor
   * @param {string} token
   */
  constructor(spellyAPI) {
    this.api = spellyAPI
    this._cache     = {};
    this.threads    = 2;
    this.chunk_size = -1;

    this._id      = 0;
    this._threads = {};

    this.onError   = () => {
    };
    this.onSuccess = () => {
    };

    this._onError = (...args) => {
      this.interrupt();
      this.onError(...args);
      console.log('≈≈≈ Spellchecker returned error', JSON.stringify(...args));
    };
  }

  /**
   * Check key value object for misspelled words
   *
   * @method
   * @param {object} sentences Key value object {[id]: [sentence]}
   * @param {function} onProgress
   * @param {function} onTerminated Callback that triggers when scan process terminated
   * @param {MSDocument} document
   * @returns {Promise<void>}
   */
  async check(sentences, onProgress = null, onTerminated = () => void (0), document) {

    if (!Memory.token) {
      console.log(' ⚠️ CHUNK CHECK: NO TOKEN FOUND!');
      return null;
    }

    const threadId = this._id++;

    this._threads[threadId] = true;

    onProgress = typeof onProgress === 'function' ? onProgress : (data) => {
    }

    let buffer = {};

    const _chunks = this.chunk_size <= 0 ? [sentences] : chunkAssoc(sentences, this.chunk_size);

    Memory.checker_total  = _chunks.length;
    Memory.checker_remain = _chunks.length;

    onProgress({
      current: 0,
      remain: Memory.checker_remain,
      total: Memory.checker_total,
    });

    let hasErrors = false;

    //console.log('BEFORE PROMISE await Promise.map(_chunks, chunk => {})');

    await Promise.map(_chunks, chunk => {
      if (!this._threads[threadId]) {
        return;
      }

      return (async () => {
        const checkedChunk = Object.keys(chunk).length ? await this.checkChunk(chunk, threadId, document) : {};

        if (checkedChunk === null) {
          console.log('⛔️ CHECKED CHUNK ERROR!');
          hasErrors = true;
          return;
        }

        if (!this._threads[threadId]) {
          return;
        }

        Memory.checker_remain--;

        if (!this._threads[threadId]) {
          return;
        }

        if (this._threads[threadId]) {
          onProgress({
            current: (Memory.checker_total - Memory.checker_remain) || 0,
            remain: Memory.checker_remain || 0,
            total: Memory.checker_total || 0,
            terminated: !this._threads[threadId],
          });
        }

        // buffer = {
        //     ...buffer,
        //     ...checkedChunk,
        // }
        Object.keys(checkedChunk).forEach(key => {
          buffer[key] = checkedChunk[key];
        });
      })();
    }, {
      concurrency: this.threads,
    });

    //console.log('AFTER PROMISE await Promise.map(_chunks, chunk => {})');

    if (this._threads[threadId]) {
      onProgress({
        current: 0,
        remain: 0,
        total: 0,
      });
    }

    if (!this._threads[threadId]) {
      return null;
    }

    delete this._threads[threadId];

    if (!hasErrors) {
      console.log(' ✅️ Checking process DONE!');
      this.onSuccess();
    } else {
      return null;
    }

    return buffer;
  }

  /**
   * Network check key value sentences chunk
   *
   * @method
   * @param {object} sentences
   * @param threadId
   * @param {MSDocument} document
   * @returns {Promise<*>}
   */
  async checkChunk(sentences, threadId, document) {

    if (!this._threads[threadId]) {
      console.warn('⚠️ CHUNK CHECK: THREAD STOPPED');

      return true;
    }

    // const token = Memory.token || '';

    if (!this._threads[threadId]) {
      console.warn('⚠️ CHUNK CHECK: THREAD STOPPED');
      return true;
    }

    let _sentences;
    if (this.chunk_size <= 0) {
      const uncompressedSentencesLength = Object.keys(sentences).length;
      // TODO: [SD-253]
      _sentences                        = sentences_compress(sentences);
      const totalSentencesLength        = Object.keys(_sentences).length;
    } else {
      _sentences = sentences;
    }

    // fix padding
    Object
      .keys(_sentences)
      .forEach(i => (_sentences[i].startsWith(' ') &&
                     (_sentences[i] = _sentences[i].replace(/^\s*/g, p => '*'.repeat(p.length)))));

    // TODO: optimize due conflict/collision override ids


    const langCode = Memory.language || 'en';

    let response = await this.api.check({
          'lang': langCode,
          'sentences': _sentences,
        })

    //////console.log("Check Check Response", response)
    if (!response.result) {
      console.warn('⚠️ CHUNK CHECK: UNSUCCESSFUL RESPONSE');

      if (!this._threads[threadId]) {
        console.warn('⚠️ CHUNK CHECK: THREAD STOPPED');
        return true;
      }

      if (typeof this._onError === 'function') {
        this._onError(response);
      }
      return null;
    }

    Memory.is_trial      = response.is_trial;
    Memory.is_licensed   = response.is_licensed;
    Memory.trial_left    = response.trial_left;
    Memory.licensed_left = response.licensed_left;

    response = response.data;

    return response;
  }

  get checking() {
    return Object.keys(this._threads) > 0;
  }

  /**
   * Interrupt current check process
   *
   * @method
   */
  interrupt() {
    Object.keys(this._threads).forEach(id => this._threads[id] = false);
  }

}
