import sleep from "../../helpers/sleep";

/**
 * @class
 */
export default class SketchAction {
    /**
     * @constructor
     * @param {*} ref class reference to native action (for example: MSZoomToSelectionAction)
     */
    constructor(ref) {
        this.ref = ref;
    }

    /**
     * Perform action event
     * @method
     * @param {object|null} eventArgs
     * @param {number} delay
     * @returns {Promise<*>}
     */
    async invoke(eventArgs = {}, delay = 350) {
        const buffer = this.ref.doPerformAction(eventArgs);
        await sleep(delay);
        return buffer;
    }
}