import fs from '@skpm/fs';
import os from '@skpm/os';
import path from '@skpm/path';
import { API_HOST, api_version } from './consts';
import { spellyDir } from './consts.additional';
import md5 from 'md5';
import Memory from '../modules/Memory';
import SourceMap from '../packages/SourceMap';
import { encodingFromOptions } from '@skpm/fs/utils';
import { Buffer } from 'buffer';
import { serialize } from './core.helpers';

let delta = +new Date();

/**
 * @param {Error|string|any|any[]} e
 * @param {string} type
 * @param {string} level
 */
export default function handleLog(e, type = 'notice', level = handleLog.MEDIUM) {
  if (Array.isArray(e) && e.length === 1 && typeof e === 'object' && e[0] instanceof Error) {
    e = e[0];
  }

  if (e instanceof Error) {
    return SourceMap.debug(e);
  }

  let fileName;
  let deltaTMP = +new Date();
  // let message = '[' + (new Date().toLocaleString()) + '][' + level + '][+' + Math.round(deltaTMP - delta) + '] ' + description + ': ';
  let message  = (0 ? '[' + (handleLog.TYPE_SHORT_ALIAS[level] || 'VERB').toUpperCase() + '] ' : '') +
                 (0 ? (handleLog.ICONS[type] || '🛠') + ' ' : '');

  delta = deltaTMP;

  if (typeof e === 'string') {
    fileName = handleLog.LOG_FILE;
    message += `${e}`;
  } else if (Array.isArray(e)) {
    fileName = handleLog.LOG_FILE;
    e        = e.filter(e => typeof e !== 'function' && typeof e !== 'undefined');
    message += `${e.map(e => typeof e === 'string' || !e ? e : serialize(e, { min: true, colors: true })).join(' ')}`;
  } else if (e instanceof Error || (typeof e.stack !== 'undefined' && typeof e.message !== 'undefined')) {
    fileName = handleLog.ERROR_FILE;
    fileName = handleLog.LOG_FILE;
    message += `${e.name} ${e.message.replace(/\n/gi, '\n\t')}\n\t${e.stack.replace(/\n/gi, '\n\t')}`;
  } else {
    return;
  }

  switch (type) {
    case 'raw':
      break;

    case 'console.log':
      // message = chalk.gray(/*'LOG: ' + */message);
      break;
    case 'console.info':
      message = chalk.rgb(52, 152, 219)(/*'INFO: ' + */message);
      // message = chalk.rgb(52, 152, 219)(' INFO ') + ' | ' +
      //     message.replace(/\n/g, '\n' + ' '.repeat(' INFO '.length) + ' | ');
      break;
    case 'console.warn':
      message = chalk.rgb(243, 156, 18)(/*'WARNING: ' + */message);
      // message = chalk.rgb(243, 156, 18)(' WARNING ') + ' | ' +
      //     message.replace(/\n/g, '\n' + ' '.repeat(' WARNING '.length) + ' | ');
      break;
    case 'deprecation':
      message = chalk.rgb(243, 156, 18).rgb(199, 37, 78)(/*'DEPRECATION: ' + */message);
      // message = chalk.rgb(249, 242, 244).rgb(199, 37, 78)(' DEPRECATION ') + ' | ' +
      //     message.replace(/\n/g, '\n' + ' '.repeat(' DEPRECATION '.length) + ' | ');
      break;
    case 'refactor':
      // message = chalk.rgb(231, 76, 60)(/*' !REFACTOR ') + ' | ' +
      //     message.replace(/\n/g, '\n' + ' '.repeat(' REFACTOR '.length) + ' | ');
      message = chalk.rgb(52, 152, 219)(/*'REFACTOR: ' + */message);
      break;
    case 'exception':
      // message = chalk.red(message);
      break;
    case 'console.error':
      // message = chalk.rgb(231, 76, 60)(/*' ERROR ') + ' | ' +
      //     message.replace(/\n/g, '\n' + ' '.repeat(' ERROR '.length) + ' | ');
      message = chalk.rgb(231, 76, 60)(/*'ERROR: ' + */message);
      break;

  }

  // if (['raw', 'console.log'].indexOf(type) < 0) {
  //   const maxLineLength = Math.max(...(message.split('\n').map(l => l.trim().length))) - 23;
  //   message = '-'.repeat(maxLineLength) + '\n' + message.trim() + '\n' + '-'.repeat(maxLineLength) + '\n';
  // }

  ensureDirExists(fileName);

  if (fs.existsSync(fileName)) {
    appendFileShim(fileName, message + '\n', 'utf8');
  } else {
    fs.writeFileSync(fileName, message + '\n', 'utf8');
  }

  if (handleLog.SUBMIT_LEVELS.indexOf(level) > -1 && e instanceof Error) {
    const minifiedCallStack = e.stack.replace(/main\.js:([0-9]+):([0-9]+)/gi, 'main.js').replace(
      /([@\/])(.+)\/main.js/gi, '').replace(/\n\n+/gi, '\n').trim();

    const hashCode = md5(JSON.stringify({
      name: e.name,
      message: e.message,
      minifiedCallStack,
      level,
      description: type,
    }));

    const token = Memory.token || '';

    const screen = null;

    const DATA = {
      hash: hashCode,
      name: e.name,
      message: e.message,
      stack: e.stack,
      level: level,
      logs: tryCatch(() => fs.readFileSync(handleLog.LOG_FILE).toString(), ''),
      errors: tryCatch(() => fs.readFileSync(handleLog.ERROR_FILE).toString(), ''),
      screen,
      version: {
        ...api_version,
      },
      os: {
        arch: os.arch(),
        cpus: os.cpus().length,
        freemem: os.freemem(),
        totalmem: os.totalmem(),
        hostname: os.hostname(),
        loadavg: os.loadavg(),
        networkInterfaces: os.networkInterfaces(),
        platform: os.platform(),
        release: os.release(),
        type: os.type(),
        uptime: os.uptime(),
        userInfo: os.userInfo(),
      },
    };

    (async () => {
      let response = await fetch(API_HOST + '/api/report?access_token=' + token.replace(/=/g, '%3D'), {
        method: 'post',
        body: JSON.stringify(DATA),
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
      }).catch(() => 0);

      if (!response) {
        return null;
      }

      response = null;
    })();

    // response = await response.json();
    // console.log('🌎 JSON RESPONSE: ' + JSON.stringify(response, null, 2));

  }
}

handleLog.refresh = function() {
  if (global['@handleLog_init']) {
    return;
  }
  global['@handleLog_init'] = true;
  if (fs.existsSync(handleLog.LOG_FILE)) {
    const hFile = NSFileHandle.fileHandleForUpdatingAtPath(handleLog.LOG_FILE);
    hFile.truncateFileAtOffset(0);
    hFile.closeFile();
  }
  if (fs.existsSync(handleLog.ERROR_FILE)) {
    const hFile = NSFileHandle.fileHandleForUpdatingAtPath(handleLog.ERROR_FILE);
    hFile.truncateFileAtOffset(0);
    hFile.closeFile();
  }
};

handleLog.LOG_FILE   = path.join(spellyDir, 'spelly.log');
handleLog.ERROR_FILE = path.join(spellyDir, 'spelly.error.log');

handleLog.CRITICAL = 'Highest';
handleLog.HIGHEST  = 'Highest';
handleLog.HIGH     = 'High';
handleLog.MEDIUM   = 'Medium';
handleLog.LOW      = 'Low';
handleLog.LOWEST   = 'Lowest';

handleLog.VERB = {
  [handleLog.CRITICAL]: 'CRITICAL',
  [handleLog.HIGHEST]: 'HIGHEST',
  [handleLog.HIGH]: 'HIGH',
  [handleLog.MEDIUM]: 'MEDIUM',
  [handleLog.LOW]: 'LOW',
  [handleLog.LOWEST]: 'LOWEST',
};

handleLog.ICONS = {
  'notice': '💡️',
  'console.log': '⚪️',
  'console.info': '🔵',
  'console.warn': '🟡',
  'console.error': '🔴️',
  'exception': '⛔️',
};

handleLog.TYPE_SHORT_ALIAS = {
  'notice': 'notice',
  'console.log': 'verb',
  'console.info': 'info',
  'console.warn': 'warn',
  'console.error': 'error',
  'exception': 'error',
};

handleLog.SUBMIT_LEVELS = [
  handleLog.CRITICAL,
  handleLog.HIGHEST,
  handleLog.HIGH,
  handleLog.MEDIUM,
];

const ensureDirExists = (writePath) => {
  const dir = path.dirname(writePath);

  try {
    if (fs.existsSync(dir)) {
      return true;
    }
  } catch (e) {
  }

  try {
    fs.mkdirSync(dir, { recursive: true });
    return true;
  } catch (e) {
  }

  return false;
};

function appendFileShim(file, data, options = 'utf8') {
  // if (!fs.existsSync(file)) {
  //   //   return fs.writeFileSync(file, data, options);
  //   // }
  // const handle = NSFileHandle.fileHandleForWritingAtPath(file);
  const handle = NSFileHandle.fileHandleForUpdatingAtPath(file);
  handle.seekToEndOfFile();
  const encoding = encodingFromOptions(options, 'utf8');
  const nsdata   = Buffer.from(
    data,
    encoding === 'NSData' || encoding === 'buffer' ? undefined : encoding,
  ).toNSData();
  handle.writeData(nsdata);
  handle.closeFile();
}

export function tryCatch(callback, defaultValue = null, { reject = e => e, resolve = e => e } = {}) {
  try {
    return resolve(callback());
  } catch (e) {
    reject(e);
    console.log("Global try catch")
    console.log(e)
  }


  return defaultValue;
}