  /**
   * @constructor
   */
  constructor(document, spellyAPI) {
    this._activation_error = null;

    this.spellyAPI = spellyAPI;

    this.spelling = new Spellchecker(this.spellyAPI);

    this.taskPool = new TaskPool({
      timeout: 1000,
      autoStart: false,
    });

    this.onCheckComplete = new TaskPool();
    this.onWidgetResolve = new TaskPool();

    this.document = document;

    this.cachedCheckLayer = null;
    this.sentencesUtility = STUtil.alloc().init()
    this.sentencesUtility.document = document

    this.widgets = {};

    this.spelling.onError = async error => {
      if (!error.auth && !error.activate) {
        return;
      }

      this.spelling.interrupt();

      this.setStateToAll({
        activated: false,
      });

      const unlock = () => {
        this.setStateToAll({
          activated: true,
        });

        if (this.cachedCheckLayer && error.auth || error.activate) {
          this.globalCheck(this.cachedCheckLayer);
        }
      };
    };

    this.scanner_pool         = [];
    this.scanner_locked       = false;
    this.scanner_frozen       = false;
    this.scanner_freeze_timer = null;
  }

  queue() {
    return this.scanner_pool;
  }

  unfreeze(document = null) {
    if (this.scanner_freeze_timer) {
      clearTimeout(this.scanner_freeze_timer);
      this.scanner_freeze_timer = null;
    }
    this.scanner_frozen = false;
    if (document) {
      this.globalCheck(null, document);
    }
  }

  freeze(timeout = 15000) {
    if (this.scanner_freeze_timer) {
      clearTimeout(this.scanner_freeze_timer);
      this.scanner_freeze_timer = null;
    }

    this.scanner_frozen = true;

    if (timeout > 0) {
      this.scanner_freeze_timer = setTimeout(() => {
        this.scanner_frozen       = false;
        this.scanner_freeze_timer = null;
      }, timeout);
    }
  }

  setStateToAll(state) {
    Object.values(this.widgets).forEach(e => {
      if (typeof state === 'function') {
        const buffer = state(e);
        if (typeof buffer === 'object') {
          e.setState(buffer);
        }
      } else {
        e.setState(state);
      }
    });
  }

  isActivated() {
    return !this._activation_error;
  }

  async run() {
  }

  /**
   * Cleaning up memory
   *
   * @method
   * @param document
   */
  dealloc(document) {
     const _document = sketch.fromNative(document);
    if (this.widgets[_document.id]) {
      delete this.widgets[_document.id];
    }
  }

  async getAsyncWidget(document) {
    return await waitFor(() => this.getWidget(document));
  }

  getWidget(document) {
    document = getDocumentFrom(document);
    try {
      return this._getWidget(document);
    } catch (e) {
      // TODO: CRITICAL ERROR, REINITIALIZE PLUGIN
      // use after free bug
      // TODO: free
      console.error('[Widget] initialization error', e);
      this.dealloc(document);
      handleLog(e, '[Widget] initialization error', handleLog.MEDIUM);
    }

    return null;
  }

