import PanelXib from "../../../spelly-sketch/HeaderPanel.xib";

import Data from "../helpers/data";
import WebKitForm from "./WebKitForm";
import {getTheme} from "../helpers/helpers";
import {api_version} from "../helpers/consts";

/**
 * WidgetForm
 * Main widget panel class
 * @class
 */
export default class WidgetForm extends WebKitForm {

  /**
   * @constructor
   * @param {MSDocument|null} document optional reference to document place on widget
   */
  constructor(document) {
    super(document, PanelXib(), Data.get('widget.view'));

    this.min_size = {
      width: 70,
      height: 70,
    };
    this.max_size = {
      width: 640,
      height: 330,
    };


    this.on('visibility', ({start, finish, state}) => {
      if (state && start) {
        this.rect = {...this.max_size};
      } else if (!state && finish) {
        this.rect = {...this.min_size};
      }
    });

    this.on('boot', () => {
      this.setState({
        theme: getTheme(),
        version: api_version,
      });
    });


  }

  /**
   * Getter for widget frame rectangle
   *
   * @property
   * @returns {{offset: {x: number, y: number}, x: number, width: *, y: number, height: number}|*}
   */
  get rect() {
    return this._rect;
  }

  /**
   * Setter for widget frame rectangle
   *
   * @property
   * @param {{offset: {x: number, y: number}, x: number, width: *, y: number, height: number}|*} rect
   */
  set rect(rect) {
    rect = rect || {};

    rect.x = rect.x || this._rect.x || 0;
    rect.y = rect.y || this._rect.y || 0;
    rect.width = rect.width || this._rect.width || 0;
    rect.height = rect.height || this._rect.height || 0;

    this._rect.x = rect.x;
    this._rect.y = rect.y;
    this._rect.width = rect.width;
    this._rect.height = rect.height;

    const size = this.canvasRect;

    this.view.setFrame(NSMakeRect(
        size.width - (this._rect.offset.x + rect.x + this._rect.width),
        this._rect.offset.y,
        this._rect.width,//size.width - Math.round(this._rect.offset.x * 2),
        rect.height,
    ));
  }

  /**
   * Get Sketch drawing canvas reference
   *
   * @property
   * @returns {NSView}
   */
  get canvas() {
    if (!this.document) {
      return null;
    }
    return this.document
        .canvasPlaceholderView();
  }

  /**
   * Get frame rectangle reference
   *
   * @property
   * @returns {object} frame reference
   */
  get canvasRect() {
    return this.canvas
        .visibleRect()
        .size;
  }

  /* methods */
  /**
   * Reset canvas rect to default
   *
   * @method
   */
  resetRect() {
    const size = this.canvasRect;

    if (!size) {
      return;
    }

    this._rect = {
      x: 0,
      y: 0,

      // width: size.width,
      // height: 54,

      ...this.min_size,

      offset: {
        x: 20,
        y: 20,
      },
    };

    this.rect = {};
  }

  /**
   * Refresh NSView and add current form as subview
   *
   * @method
   */
  updateView() {
    if (this.document) {
      this.resetRect();
      this.canvas.addSubview(this.view);
    }
  }
}
