import { main_thread } from '../helpers/helpers';
import semver from 'semver';

const REPOSITORY        = __manifest.repository;
const BRANCH            = __manifest.branch;
const STREAM            = REPOSITORY + '/' + BRANCH;
const REMOTE_SCRIPT_DIR = 'https://github.com/' + REPOSITORY + '/raw/' + BRANCH + '/spelly.sketchplugin/Contents/Sketch/';
const MANIFEST_FILE     = 'manifest.json';

export default class Updater {
  static remoteManifest = null;
  static checked        = false;

  static async _get(url) {
    // @refactor
    return await fetch(url, { headers: { 'User-Agent': 'SpellyChecker v' + __manifest.version } });
  }

  static async _get_file(url) {
    return (await this._get(url)).blob();
  }

  static async _getManifest() {
    return this.remoteManifest = this.remoteManifest ||
                                 (await this._get(REMOTE_SCRIPT_DIR + MANIFEST_FILE))
                                   .json();
  }

  static async _getRemoteFile(filename): NSData {
    return await this._get_file(REMOTE_SCRIPT_DIR + filename);
  }

  // @refactor
  static async checkNewPluginVersion() {
    if (this.checked) {
      return;
    }
    this.checked = true;

    try {
      const pluginManager = AppController.sharedInstance().pluginManager(),
            r_manifest    = await this._getManifest();

      if (!r_manifest || !r_manifest.version || !semver.gt(r_manifest.version, __manifest.version)) {
        return;
      }

      const block = __mocha__.createBlock_function('v8@?0', () => {
        main_thread(() => {
          pluginManager.reloadPlugins();
          console.log('#'.repeat(64));
          console.log('#'.repeat(32) + ' FINISHED!!!!');
          console.log('#'.repeat(64));
        });
      });

      console.info('updated queued');
      pluginManager.downloadAndInstallPluginWithIdentifier_version_downloadCompletionHandler(__manifest.identifier, r_manifest.version, block);
    } catch (e) {
      console.error(e);
      console.exception(e);
      console.warn('update terminated');
    }
  }

  static reloadPlugin() {
    throw new Error('Not implemented');
  }

  static restoreBackup(v) {
    throw new Error('Not implemented');
  }

  static getLatest() {
  }
}
