import PanelXib from '../../../spelly-sketch/HeaderPanel.xib';

import Data from '../helpers/data';
import WebKitForm from './WebKitForm';
import Memory from '../modules/Memory';

/**
 * Bootscreen
 *
 * @class
 */
export default class Bootscreen extends WebKitForm {
  constructor(document, spelly, api) {
    console.log(' ✅️ [Bootscreen] constructor ')
    super(document, PanelXib(), Data.get('bootscreen.view'));
    // this.webkit.visible = true;
    // this.webkit.frameView = this.view;
    this.view.autoresizingMask = NSViewWidthSizable | NSViewHeightSizable;
    this.canvas.addSubview(this.view);
    this.frameView = this.canvas;
    this.framework = framework;
    this.on('close', () => this.hide());

    this.on('openLink', (link) => {
        this.api.openURL(link)
    })
    this.spelly = spelly;
    this.api = api;
  }

  async getStarted(data) {
    this.call('vm.postGetStarted', await this.api.getStarted(data));
  }

  async validatePin(data) {
    this.call("vm.postValidatePin",await this.api.validatePin(data))
  }

  async validatePassword(data){
    this.call("vm.postValidatePin",await this.api.validatePassword(data))
  }

  async resendPin(data) {
    console.log("Resend pin back")
    this.call("vm.postResendPin", await this.api.resendPin(data))
  }
  async startValidate() {
    if ( Memory.token ) {
      this.spelly.setStateToAll({ 'activated': true })
      await this.spelly.globalCheck(this.document, this.document)
    } else {
      console.log("No Token - No Validation")
    }
  }


  setDocument(document: MSDocument) {
    this.view.removeFromSuperview();
    this.document = document;
    this.canvas.addSubview(this.view);
  }

  /**
   * Get Sketch drawing canvas reference
   *
   * @property
   * @returns {NSView}
   */
  get canvas() {
    if (!this.document) {
      return null;
    }
    return this.document.canvasPlaceholderView();
  }

  /**
   * Get frame rectangle reference
   *
   * @property
   * @returns {object} frame reference
   */
  get canvasRect() {
    return this.canvas.visibleRect().size;
  }
}