import MochaJSDelegate from "../MochaJSDelegate";

/**
 * Internal delegate binding for <strong>WebKit</strong>
 *
 * @function
 * @param {NSWebKit|WebKitForm} hostForm
 * @returns {MochaJSDelegate}
 */
export function WebViewDelegate(hostForm) {
    return new MochaJSDelegate({
        "webView:didFinishLoadForFrame:": ((webView, webFrame) => {
            hostForm.windowObject.evaluateWebScript('window.console.toast = (...params) => { call(\'toast\', ...params); }');
            hostForm.windowObject.evaluateWebScript('window.console.log = (...params) => { call(\'log\', ...params); }');
            hostForm.windowObject.evaluateWebScript('window.call = function(func, ...params) { return alert(JSON.stringify({ func, params })); }');
        }),
    });
}
export function WebViewUIDelegate(hostForm) {
    return new MochaJSDelegate({
        "webView:runJavaScriptAlertPanelWithMessage:": ((webView, payload) => {
            /**
             * @type {{func: string, params: array}}
             */
            const data = JSON.parse(payload);

            if (typeof hostForm[data.func] === 'function') {
                hostForm[data.func].apply(hostForm, data.params);
            } else {
                hostForm.emit(data.func, ...data.params);
            }
        }),
    });
}


export default WebViewDelegate;