const handleLog = require('./helpers/reporter').default;

/**
 * Default logs are unstable. This code helps make all logs clear and easy to read
 * @SOLVED_ISSUES:
 * 1. unexpected wrapping to quotes in terminal
 * 2. unexpected shifting
 * 3. Logs Should not appear for production version AT ALL, so this code trying to solve this problem
 *
 * @NOTE: issues with logs
 * 1. Sometime logs still works unexpected. We should constantly improve this code
 */
export default function() {
  const {
    DEBUG,
    LOG_FILTER,
    VERB_ERROR,
    VERB_INFO,
    VERB_LOG,
    VERB_WARN,
    VERB_PRINT,
    VERB_PRINTF,
    VERB_DEPRECATION,
    VERB_REFACTOR,
    VERBOSE_REPORT,
    VERBOSE,
  } = require('./helpers/consts');

  const console_log = console.log.bind(console);

  if (DEBUG) {
    if ((VERBOSE & VERB_LOG) === 0) console.log = () => 0;
    if ((VERBOSE & VERB_INFO) === 0) console.info = () => 0;
    if ((VERBOSE & VERB_WARN) === 0) console.warn = () => 0;
    if ((VERBOSE & VERB_ERROR) === 0) console.error = console.exception = () => 0;
    if ((VERBOSE & VERB_PRINT) === 0) console.print = () => 0;
    if ((VERBOSE & VERB_DEPRECATION) === 0) global['deprecation'] = () => 0;
    if ((VERBOSE & VERB_REFACTOR) === 0) global['refactor'] = () => 0;
    if ((VERBOSE & VERB_PRINTF) === 0) global['printf'] = () => 0;
  } else {
    console.log           = () => 0;
    console.info          = () => 0;
    console.warn          = () => 0;
    console.error         = () => 0;
    console.error         = console.exception = () => 0;
    console.print         = () => 0;
    global['deprecation'] = () => 0;
    global['refactor']    = () => 0;
    global['printf']      = () => 0;
  }

  if (DEBUG && LOG_FILTER) {
    console.print = console.log = console.info = console.warn = console.error = (
      input, ...rest) => {
      if (typeof input !== 'string') {
        return 0;
      }

      if (
        (typeof LOG_FILTER === 'string' && !input.includes(LOG_FILTER)) ||
        (typeof LOG_FILTER === 'object' && LOG_FILTER.match && !input.match(LOG_FILTER))
      ) {
        return 0;
      }

      return console_log(input, ...rest);
    };
  }

  if (DEBUG && VERBOSE_REPORT) {
    if (!!(VERBOSE_REPORT & VERB_LOG)) console.log = function(...args) {
      handleLog(args, 'console.log', handleLog.LOWEST);
    };
    if (!!(VERBOSE_REPORT & VERB_ERROR)) {
      console.exception = function(...args) {
        handleLog(args, 'exception', handleLog.CRITICAL);
      };
      console.error     = function(...args) {
        handleLog(args, 'console.error', handleLog.HIGHEST);
      };
    }
    if (!!(VERBOSE_REPORT & VERB_WARN)) console.warn = function(...args) {
      handleLog(args, 'console.warn', handleLog.HIGH);
    };
    if (!!(VERBOSE_REPORT & VERB_INFO)) console.info = function(...args) {
      handleLog(args, 'console.info', handleLog.LOW);
    };
    const _deprecation = deprecation.bind(global);
    if (!!(VERBOSE_REPORT & VERB_DEPRECATION)) global['deprecation'] = function(...args) {
      handleLog(args, 'deprecation', handleLog.LOW);
      _deprecation(...args);
    };
    const _refactor = refactor.bind(global);
    if (!!(VERBOSE_REPORT & VERB_REFACTOR)) global['refactor'] = function(...args) {
      handleLog(args, 'refactor', handleLog.LOW);
      _refactor(...args);
    };
    const _printf = printf.bind(global);
    if (!!(VERBOSE_REPORT & VERB_PRINTF)) global['printf'] = function(...args) {
      handleLog(args, 'printf', handleLog.LOW);
      _printf(...args);
    };
  }
};



