export function deepSearch(object, callback) {
  if (Array.isArray(object)) {
    return object.reduce((a, o) => a.concat(deepSearch(o, callback)), []);
  }

  const cb = v => {
    try {
      return callback(v);
    } catch (e) {
    }
    return false;
  };

  if (cb(object)) {
    return [object];
  }

  let result = [], propName;

  for (propName in object) {
    if (object.hasOwnProperty(propName) && typeof object[propName] === 'object') {
      let buffer = deepSearch(object[propName], callback);
      if (buffer.length) {
        result = result.concat(buffer);
      }
    }
  }

  return result;
}

export function chunkExec(input, callback, size = 50, { onDone = () => 0, interval = 250 } = {}) {
  let timer,
      canceled = false,
      chunks   = Array.isArray(input) ? input.chunk(size) : chunkAssoc(input, size);

  function cancel() {
    canceled = true;
    if (timer) {
      clearTimeout(timer);
      timer = null;
    }
  }

  function next(chunks) {
    let chunk;

    if (!(chunk = chunks.shift())) {
      return onDone();
    }

    // (async () => await callback(chunk))();
    callback(chunk);

    if (canceled) {
      onDone();
    }

    timer = setTimeout(chunks => next(chunks), interval, chunks);
  }

  next(chunks);

  return cancel;
}

export async function waitFor(cb: Function, defaultValue = null, { retries = 10, delay = 100 } = {}) {
  let tmp = await cb();

  if (tmp) {
    return tmp;
  }

  if (retries <= 0) {
    return defaultValue;
  }

  await sleep(delay);

  return await waitFor(cb, defaultValue, { retries: retries - 1, delay });
}

export function tryCatch(callback, defaultValue = null, { reject = e => e, resolve = e => e } = {}) {
  try {
    return resolve(callback());
  } catch (e) {
    reject(e);
  }

  return defaultValue;
}

/**
 * Transform HEX color #FFFFFF -> NSColor pointer
 *
 * @function
 * @param hex
 * @param alpha
 * @returns {*}
 */
export function hex2color(hex, alpha) {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex),
        red    = parseInt(result[1], 16) / 255,
        green  = parseInt(result[2], 16) / 255,
        blue   = parseInt(result[3], 16) / 255;

  return NSColor.colorWithCalibratedRed_green_blue_alpha(
    red,
    green,
    blue,
    (typeof alpha !== 'undefined') ? alpha : 1,
  );
}

/**
 * Extract text layer
 * @param layer
 * @return {MSTextLayer[]|MSImmutableTextLayer[]}
 */
export function extractTextLayers(layer) {
  if (layer instanceof MSTextLayer || layer instanceof MSImmutableTextLayer) {
    return [layer];
  }

  if (layer instanceof MSOverrideRepresentation) {
    let buffer = [], tmp = toArray(layer.children());

    if (!tmp.length) {
      return extractTextLayers(layer.modifiedParentMaster());
    }

    tmp.forEach(layer => {
      buffer = buffer.concat(extractTextLayers(layer));
    });

    return buffer;
  }

  if (layer instanceof MSSymbolInstance) {
    let buffer = [];
    toArray(layer.overrideContainer().children()).forEach(layer => {
      buffer = buffer.concat(extractTextLayers(layer));
    });
    return buffer;
  }

  if (layer instanceof MSSymbolOverride) {
    let buffer = [];
    toArray(layer.internalChildren()).forEach(layer => {
      buffer = buffer.concat(extractTextLayers(layer));
    });
    return buffer;
  }

  if (layer instanceof MSAvailableOverride) {
    return extractTextLayers(layer.master());
  }

  // if (layer instanceof MSImmutableSymbolInstance) {
  //     return extractTextLayers(layer.cachedModifiedSymbolMaster());
  // }

  if (layer instanceof MSDocument) {
    let buffer = [];

    try {
      toArray(layer.pages()).forEach(page => {
        buffer = buffer.concat(extractTextLayers(page));
      });
    } catch (e) {
      handleLog(e, 'exception', handleLog.HIGH);
      console.error('[Extracting TextLayers Exception]', e);
    }

    return buffer;
  }

  if (
    layer instanceof MSPage ||
    layer instanceof MSSymbolMaster ||
    layer instanceof MSImmutableSymbolMaster ||
    layer instanceof MSArtboardGroup ||
    layer instanceof MSLayerGroup ||
    (layer.layers && typeof layer.layers === 'function')
  ) {
    let buffer = [];
    try {
      toArray(layer.layers()).forEach(layer => {
        buffer = buffer.concat(extractTextLayers(layer));
      });
    } catch (e) {
      handleLog(e, 'exception', handleLog.HIGH);
      console.error('[Extracting TextLayers Exception]', e);
    }
    return buffer;
  }

  return [];
}

/**
 * Normalize word offset in sentence
 *
 * @param {string} text
 * @param {number} line
 * @param {number} offset
 * @returns {number}
 */
export function normalizeOffset(text, line, offset) {
  return text
           .split('\n')
           .reduce(
             (o, l, i) => i + 1 >= line ? o : o + l.length + 1, 0,
           ) + offset;
}

/**
 * Highlight giver range in layer text
 *
 * @deprecated
 * @function
 * @param {MSTextLayer|_MSTextLayer} layer
 * @param range
 * @returns {Promise<void>}
 */
export async function highlightLayerText(layer, range) {
  // TODO: SD-59
  const style = layer.attributedStringValue();

  // layer.adjustFrameToFit();

  const nsRange = NSMakeRange(range.offset, range.length);
  const nsColor = hex2color('FF0000');

  layer.setIsEditingText(true);
  layer.addAttribute_value_forRange(NSUnderlineColorAttributeName, nsColor, nsRange);
  layer.addAttribute_value_forRange(NSUnderlineStyleAttributeName, NSUnderlineStyleThick, nsRange);
  layer.addAttribute_value_forRange(NSForegroundColorAttributeName, nsColor, nsRange);
  layer.setIsEditingText(false);

  await sleep(200);
  layer.reapplyPreviousAttributesFromString(style);
  await sleep(80);
}

/**
 * Transform
 *
 * @function
 * @param {{offset:number, length: number}} area
 * @returns {{offset: number, length: number}}
 */
export function areaToRange(area) {
  return {
    offset: area.offset,
    length: area.length,
  };
}

/**
 * Chunk associated key->value object to array of chunked sets
 *
 * @function
 * @param {object} object
 * @param {number} size
 * @returns {Array}
 */
export function chunkAssoc(object, size) {
  const chunks = [];
  const values = Object.values(object);
  let i        = 0;
  let chunk    = {};

  for (const key in object) {
    if (!object.hasOwnProperty(key)) {
      continue;
    }

    if (i !== 0 && i % size === 0) {
      chunks.push(chunk);
      chunk = {};
    }

    chunk[key] = values[i++];
  }

  chunks.push(chunk);

  return chunks;
}

