/**
 * Linear interpolation
 *
 * @param {number} a From
 * @param {number} b To
 * @param {number} n Step
 * @returns {number} Current state
 */
export function lerp(a, b, n) {
  return (1 - n) * a + n * b;
}

/**
 * @todo rework to object properties instead of one property
 *
 * @param {number} from
 * @param {number} to
 * @param {number} time Time in milliseconds to end interpolation
 * @param {function(number)} stepCallback Iteration callback
 * @param id
 * @returns {Promise<void>}
 */
export async function lerp_transition(from, to, time, stepCallback, id) {
  const iid = (Math.random() * 100000 + new Date()).toString();

  if (!id) {
    id = (Math.random() * 100000 + new Date()).toString();
  }

  LERP_CACHE[id] = iid;

  const steps = Math.abs(to - from);

  for (let i = 0; i < steps; i++) {
    if (LERP_CACHE[id] !== iid)
      return;

    from = lerp(from, to, 0.1);
    stepCallback(Math.ceil(from));
    await sleep(Math.round(time / steps));

    if (Math.abs(from - to) < 1) {
      break;
    }
  }

  stepCallback(to);

  delete LERP_CACHE[id];
}

const LERP_CACHE = {};

/**
 * Change NSView frame to given rectangle
 *
 * @function
 * @deprecated
 * @param {NSView} view
 * @param {{x: number|function, y: number|function, w: number|function, h: number|function}} rect
 */
export function setFrame(view, rect) {
  rect = rect || {};

  const origin = getFrame(view);

  return view.setFrame(NSMakeRect(
    typeof rect.x === 'function' ? rect.x(origin.x) : rect.x || origin.x,
    typeof rect.y === 'function' ? rect.y(origin.y) : rect.y || origin.y,
    typeof rect.w === 'function' ? rect.w(origin.w) : rect.w || origin.w,
    typeof rect.h === 'function' ? rect.h(origin.h) : rect.h || origin.h,
  ));
}

/**
 *
 * @param {NSView} view
 * @param defaultRect
 * @returns {{w: (*|number|Function|value.w|{}), x: (*|number), h: (*|number|Function), y: (*|number)}}
 */
export function getFrame(view, defaultRect) {
  defaultRect = defaultRect || {};

  const origin = view.visibleRect().origin;
  const size   = view.visibleRect().size;

  return {
    x: origin.x || (defaultRect.x || 0),
    y: origin.y || (defaultRect.y || 0),
    w: size.width || (defaultRect.w || 0),
    h: size.height || (defaultRect.h || 0),
  };
}

/**
 * @param layer
 */
export function extractSymbolOverrides(layer) {
  const wrappedLayer = sketch.fromNative(layer);
  return (wrappedLayer.overrides || []).reduce((accumulator, override) => {
    const attributeName = String(override.property);

    if (attributeName !== 'stringValue') {
      return accumulator;
    }

    accumulator[override.affectedLayer.id] = String(override.value);

    return accumulator;
  }, {});
}

/**
 * map overrides to their ids
 * @param {MSLayer|MSImmutableLayer} layer
 * @param {null|Array|string} attributes
 */
export function mapOverridesForLayer(layer, attributes = null) {
  const wrappedLayer = sketch.fromNative(layer);
  return (wrappedLayer.overrides || []).reduce((accumulator, override) => {
    const attributeName = String(override.property).trim().toLowerCase();

    if (Array.isArray(attributes) && attributes.indexOf(attributeName) < 0) {
      return accumulator;
    } else if (typeof attributes === 'string' && attributeName !== attributes.trim().toLowerCase()) {
      return accumulator;
    }

    accumulator[override.affectedLayer.id] = override;

    return accumulator;
  }, {});
}

export function filterByClassName(input, predicate, classes) {
  return input
    .filteredArrayUsingPredicate(
      NSPredicate.predicateWithFormat(
        classes
          .map(
            className => predicate.replace(/\?/g, '\'' + className + '\''),
          )
          .join(' || '),
      ),
    );
}

