export function getTheme() {
  return MSTheme.sharedTheme().isDark() ? 'dark' : 'light';
}

/**
 * @param {MSLayer|MSImmutableLayer|MSSymbolInstance|MSSymbolMaster} objs[]
 * @return {*}
 */
export function getDocumentFrom(...objs) {
  for (let obj of objs) {
    if (!obj) {
      continue;
    }

    if (obj instanceof MSDocument || obj instanceof MSDocumentData) {
      return obj;
    }

    if (obj.documentData) {
      return obj.documentData();
    }
  }

  return SketchAPI.document;
}

/**
 * @function
 * @param {CGRect} rect
 * @param {?({x: (number|undefined), y: (number|undefined), width: (number|undefined), height: (number|undefined)})} offset
 * @return {Rectangle}
 */
export function CGRectToRectangle(rect, offset = null) {
  return new Rectangle(
    Math.ceil(((offset || {}).x || 0) + rect.origin.x),
    Math.ceil(((offset || {}).y || 0) + rect.origin.y),
    Math.ceil(((offset || {}).width || 0) + rect.size.width),
    Math.ceil(((offset || {}).height || 0) + rect.size.height),
  );
}

/**
 * Get word relative/absolute rectangle within MSTextLayer
 * @function
 * @param {MSTextLayer} layer
 * @param {({offset: number, length: number})} area
 * @param {MSLayer|MSImmutableSymbolInstance|MSSymbolInstance} instance
 * @return {Rectangle}
 */
export function getRectangleForWord(layer, area, instance) {
  const range = areaToRange(area);
  let util = STUtil.alloc().init()
  return CGRectToRectangle(
    util.getAbsoluteWordRect_forRange_withDocument_withMaster(
      layer,
      NSMakeRange(range.offset, range.length),
      getDocumentFrom(instance, layer),
      instance || null,
    ),
  );
}

/**
 * @param {MSTextLayer} layer
 * @param {({offset: number, length: number})} area
 * @param {MSLayer|MSImmutableSymbolInstance|MSSymbolInstance} instance
 * @return {boolean}
 */
export function layerVisible(layer) {
  const rectangle = Rectangle.fromNative(layer);
  return [layer, ...findParents(layer)]
    .filter(e => !!e.absoluteRect)
    .map(e => !Rectangle.fromMSRect(e.absoluteRect()).containsRectangle(rectangle))
    .some(e => e === false);
}

/**
 * @param {MSTextLayer} layer
 * @param {({offset: number, length: number})} area
 * @param {MSLayer|MSImmutableSymbolInstance|MSSymbolInstance} instance
 * @return {boolean}
 */
export function wordVisibleInLayerBoundaries(layer, area, instance) {
  const rectangle = getRectangleForWord(layer, area, instance);
  return [layer, ...findParents(layer)]
    .filter(e => !!e.absoluteRect)
    .map(e => !Rectangle.fromMSRect(e.absoluteRect()).containsRectangle(rectangle))
    .some(e => e === false);
}

/**
 * Find all parents that matches against *parentClass
 *
 * @param {MSLayer|MSSymbolInstance|MSSymbolMaster} layer
 * @param {object[]|object|*} parentClass
 * @return {object[]|null}
 */
export function findParents(layer, parentClass = [MSArtboardGroup, MSLayerGroup, MSSymbolMaster]) {
  const parents = [];

  let parent = layer;
  while ((parent = findParent(parent, parentClass))) {
    parents.push(parent);
  }

  return parents;
}

/**
 * Find first parent that matches against *parentClass
 *
 * @param {MSLayer|MSSymbolInstance|MSSymbolMaster} layer
 * @param {object[]|object|*} parentClass
 * @return {object|null}
 */
export function findParent(layer, parentClass = [MSArtboardGroup, MSLayerGroup, MSSymbolMaster]) {
  layer = SketchAPI.findObject(String(layer.objectID()));

  let parent = layer, _parent;

  while ((parent = parent.parentObject()) && parent !== _parent) {
    if (isClassOf(parent, parentClass)) {
      return parent;
    }
    _parent = parent;
  }

  return null;
}

export function isClassOf(object, classes) {
  if (typeof classes === 'string') {
    const needle  = String(object).substr(1).split(':')[0];
    const pattern = '^(' + classes
      .split('*')
      .map(e => e.replace(/([.*+?^=!:${}()\[\]\/\\])/g, '\\$1'))
      .join('.*') + ')$';
    const test    = new RegExp(pattern, 'i').test(needle);

    console.error('PATTERN', pattern, needle, test);
    return test;
  }

  if (Array.isArray(classes)) {
    for (let classType of classes) {
      if (object instanceof classType) {
        return true;
      }
    }

    return false;
  }

  return object instanceof classes;
}

/**
 * @function
 * @param {Rectangle} rect
 * @return {boolean}
 */
export function isNullRectangle(rect) {
  return rect.x == 0 && rect.y == 0 && rect.width == 0 && rect.height == 0;
}

/**
 * @function
 * @param entityID
 * @return {MSTextLayer|null}
 */
export function MSTextLayerFromEntityID(entityID) {
  const [objectID, overrideID] = entityID.split(':');

  let layer = SketchAPI.findObject(objectID);

  if (!layer) {
    return null;
  }

  if (layer instanceof MSTextLayer) {
    return layer;
  } else if (layer instanceof MSSymbolInstance) {
    const mutableLayer = SketchAPI.findObject(overrideID);

    if (!mutableLayer) {
      return null;
    }

    /**
     * @type {MSTextLayer}
     */
    const duplicateLayer = mutableLayer.copy();

    let overrideText = '';

    sketch.fromNative(layer).overrides.forEach(e => {
      if (e.affectedLayer.id == overrideID) {
        overrideText = e.value;
      }
    });

    duplicateLayer.setStringValue(overrideText);

    return duplicateLayer;
  }

  return null;
}

/**
 * @function
 * @param {CGRect} rect
 * @param {boolean} size
 * @param {boolean} position
 * @return {string}
 */
export function CGRectString(rect, size = true, position = true) {
  return '[ CGRect ' + [
    position ? 'x = ' + rect.origin.x + ', y = ' + rect.origin.y : false,
    size ? 'width = ' + rect.size.width + ', height = ' + rect.size.height : false,
  ].filter(e => e).join(', ') + ']';
}

