/**
 * @function
 * @param {MSSymbolInstance} symbolInstance
 * @param {string|null} objectID
 * @return {MSImmutableLayer|MSImmutableTextLayer}
 */
export function findImmutableLayer(symbolInstance, objectID) {
  /*context
.document
.pages()[0]
.layers()[5]
.overrideContainer()
.children()[0]
.modifiedParentMaster()
.children()[1].objectID()*/

  for (let override of Array.from(MSAvailableOverride.flattenAvailableOverrides(symbolInstance.availableOverrides()))) {
    // console.log(override.affectedLayer(),String(override.affectedLayer().objectID()), String(objectID));
    if (String(override.affectedLayer().objectID()) == String(objectID)) {
      return override.affectedLayer();
    }
  }

  return null;
  for (let overrideRepresentation of Array.from(symbolInstance.overrideContainer().children())) {
    for (let layer of Array.from(overrideRepresentation.modifiedParentMaster().children())) {
      console.log('FOUND LAYER', layer, objectID);
      if (String(layer.objectID()) == objectID) {
        return layer;
      }
    }
  }
  return null;
}

export function main_thread(callback) {
  let util = STUtil.alloc().init()
  util.async_withCallback(() => void 0, () => callback());
}

/**
 * @param {Promise<any>|function} action
 * @return {Promise<any>}
 */
export function async(action) {
  let util = STUtil.alloc().init()
  return new Promise(resolve => {
    setImmediate(() => {
      util.async_withCallback(() => {
        return action();
      }, e => resolve(e));
    });
  });

}

export function extendObject(objectToExtend, ...objects) {
  objects.forEach(object => {
    if (typeof object !== 'object') {
      return;
    }

    Object.keys(object).forEach(key => {
      if (!object.hasOwnProperty(key)) {
        return;
      }
      objectToExtend[key] = object[key];
    });
  });
  return objectToExtend;
}

/**
 * Compress object of sentences
 *
 * @param sentences
 * @return {{}}
 */
export function sentences_compress(sentences) {
  const cache      = {};
  const _sentences = {};
  for (let sid in sentences) {
    if (!sentences.hasOwnProperty(sid)) continue;
    const sentence = String(sentences[sid]);
    if (!sentence || !sentence.trim().length) continue;

    const hash = md5(sentence);

    if (cache[hash]) {
      _sentences[sid] = '@reference:#' + cache[hash];
    } else {
      cache[hash]     = sid;
      _sentences[sid] = sentence;
    }
  }
  return _sentences;
}